html {
  background-color:cornsilk
}

div {
  border-radius: 20px;
}

.viewbtn {
  border: 2px solid seagreen;
  border-radius: 5px;
  background-color: white;
  text-align: center;
  font-size: 15px;
  padding: 10px 15px;
  transition-duration: 0.3s;
}

.viewbtn:hover {
  cursor: pointer;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  color: seagreen;
  background-color: gainsboro;
  text-decoration:underline;
}

.likebtn {
  border: none;
  background: none
}

.likebtn:hover {
  cursor: pointer;
}

.header {
  text-align: center;
  color: darkblue;
}

.header2 {
  text-align: center;
}

.blogform {
  text-align: center;
  border: 2px solid;
}

.blogformprop {
  justify-content:center;
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
}

.blogformprop input {
  margin: 5px 5px;
}

.blogs {
  border: 2px solid;
  margin-top: 10px;
}

.singleblog {
  border: 2px dashed;
  border-radius: 10px;
  display:inline-flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
  padding: 15px 20px;
  margin: 15px 23px;
}

h4 button a {
  text-decoration: none;
}